*{
  margin: 0;
  padding: 0;
}

body{
  background: hotpink;
  height: 1200px;
}

.nav{
  position: fixed;
  top: 0;
  width: 100vw;  
  height: 70px;
  background: tomato;
  align-items: center;
  display: flex;
  justify-content: center;
  box-shadow: inset 0px 2px 5px 0px #000;
}

.nav>h1{
  margin: 20px 0;
  text-align: center;
  font-family: 'Satisfy', cursive;
  font-size: 40px;
  font-weight: bold;
  text-shadow: 1px 1px 2px greenyellow;
}

.pic{
  width: 120px;
  border-radius: 10px;
}

.product{
  margin-top: 100px;
}


.oneProduct{
  margin: 10px; 
  box-shadow: 0px 0px 10px 2px #000;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 10px;
  width: 120px;
  background: #fcfcfc;
}

.productRow{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-width: 300px;
  flex-wrap: wrap;
}

.details{
  display: flex;
  flex-direction: column;
  flex-wrap: anywhere wrap;
}

.details>h1{
  margin: 6px;
  font-size: 14px;
  color:rgb(241, 71, 41);
  text-align: center;
}

.details>h4{
  
  font-size: 10px;
  font-weight: lighter;
}

.details>h3{
  margin: 3px;
  font-size: 12px;
  color: red;
}